<template lang="pug">
.checkbox
  label
    input(type="checkbox", :checked="value", @change="inputHandler")
    span#fake
    span {{ text }}
</template>

<script>
export default {
  methods: {
    inputHandler(event) {
      this.$emit('change-handler', event.target.checked)
    },
  },
  props: {
    value: {
      type: Boolean,
      default() {
        return true
      },
    },
    text: {
      type: String,
      default() {
        return ''
      },
    },
  },
}
</script>
